import { reactive, ref } from '@vue/composition-api'
import { useToast } from 'vue-toastification/composition'
import ToastificationContent from '@/@core/components/toastification/ToastificationContent.vue'
import StadisticsService from '@/services/StadisticsService'

const useHome = () => {
  const toast = useToast()

  const numeralia = reactive({
    totalAlerts: 0,
    totalAlertsOfDay: 0,
    totalDevices: 0,
    totalDrivers: 0,
    totalTeams: 0,
    totalUnits: 0,
  })

  const alertsByMonth = reactive({
    enero: 0,
    febrero: 0,
    marzo: 0,
    abril: 0,
    mayo: 0,
    junio: 0,
    julio: 0,
    agosto: 0,
    septiembre: 0,
    octubre: 0,
    noviembre: 0,
    diciembre: 0,
  })

  const isLoadingNumeralia = ref(false)
  const isLoadingtotalAlerts = ref(false)

  const getNumeralia = async () => {
    try {
      isLoadingNumeralia.value = true
      const res = await StadisticsService.getNumeralia()
      const {
        totalAlerts,
        totalAlertsOfDay,
        totalDevices,
        totalDrivers,
        totalTeams,
        totalUnits,
      } = res.data
      numeralia.totalAlerts = totalAlerts
      numeralia.totalAlertsOfDay = totalAlertsOfDay
      numeralia.totalDevices = totalDevices
      numeralia.totalDrivers = totalDrivers
      numeralia.totalTeams = totalTeams
      numeralia.totalUnits = totalUnits
    } catch (error) {
      isLoadingNumeralia.value = false
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al obtener numeralia',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const getTotalAlertsByMonth = async () => {
    try {
      isLoadingtotalAlerts.value = true
      const { data } = await StadisticsService.getTotalAlertsByMonth()
      const {
        enero,
        febrero,
        marzo,
        abril,
        mayo,
        junio,
        julio,
        agosto,
        septiembre,
        octubre,
        noviembre,
        diciembre,
        total,
      } = data

      alertsByMonth.enero = Number(enero)
      alertsByMonth.febrero = Number(febrero)
      alertsByMonth.marzo = Number(marzo)
      alertsByMonth.abril = Number(abril)
      alertsByMonth.mayo = Number(mayo)
      alertsByMonth.junio = Number(junio)
      alertsByMonth.julio = Number(julio)
      alertsByMonth.agosto = Number(agosto)
      alertsByMonth.septiembre = Number(septiembre)
      alertsByMonth.octubre = Number(octubre)
      alertsByMonth.noviembre = Number(noviembre)
      alertsByMonth.diciembre = Number(diciembre)
      alertsByMonth.total = Number(total)
    } catch (error) {
      isLoadingtotalAlerts.value = false
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al obtener alertas por mes',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  getNumeralia()
  getTotalAlertsByMonth()

  return {
    numeralia,
    isLoadingNumeralia,
    alertsByMonth,
    isLoadingtotalAlerts,
  }
}
export default useHome
